var MovieCard = ({ movie }) => {
  return (
    <div className="movie-card">
      <img
        className="movie-img"
        src={`https://image.tmdb.org/t/p/original${movie.poster_path}`}
        alt={movie.original_title}
      />
      <span>{movie.title}</span>
      <span>{movie.vote_average}</span>
    </div>
  );
};

export default MovieCard;
